@mixin text-main {
  font-size: 12px;
  line-height: 18px;
}

@mixin text-secondary {
  font-size: 10px;
  line-height: 14px;
  font-weight: 400;
  color: $secondary-text-color;
}

@mixin display-position {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

